import type { ReactNode } from "react";
import css from "./LayoutNotes.module.css";

type Props = {
  children: ReactNode;
  sidebar: ReactNode;
};

const NotesLayout = ({ children, sidebar }: Props) => {
  return (
    <section>
      <aside>{sidebar}</aside>
      <main>{children}</main>
    </section>
  );
};

export default NotesLayout;
